.codings{
    color: rgb(100, 100, 100);
    align-content: center;
    text-align: center;
    padding-bottom: 50px;
}

.outer-wrapper{
    display: inline-block;
}


@media only screen and (min-width: 768px) {
    .wrapper {
        display: grid;
        grid-template-columns: 25vw 25vw 25vw;
        grid-gap: 64px;
        text-align: left;
        animation: loadAnimation ease-in-out 1s;
    }
}

@media only screen and (max-width: 768px) {
    .wrapper {
        display: grid;
        grid-template-columns: 100vw;
        grid-gap: 64px;
        text-align: left;
        animation: loadAnimation ease-in-out 1s;
    }
}
  
.box {
    padding: 20px;
}

.f{
    display: none;
}

.codings-img-type-landscape{
    display: inline-block;
    height: auto;
    width: 100%;
}

.codings-img-type-portrait{
    display: inline-block;
    height: auto;
    width: 30%;
    padding: 0 12px 0 12px;
}

.codings-img-comingsoon{
    opacity: 0.2;
    filter: blur(12px);
}

.logo-img{
    display: inline-block;
    padding: 0 4px 0 4px;
}

a{
    text-decoration: none;
    color: rgb(201, 201, 201);
}